import React from 'react';
import styled from 'styled-components';

type SBProps = {
    title: string,
    background: string,
    opacity: number,
    borderBoxShadow: string,
    selected?: boolean,
    onClick: () => void,
};

type ContainerProps = {
    background: string,
    opacity: number,
    borderBoxShadow: string
};

const Container = styled.div<ContainerProps>`
    height: 100%;
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 12px;
    cursor: pointer;
    background: ${p => p.background};
    opacity: ${p => p.opacity};
    box-shadow: ${p => p.borderBoxShadow};
`;

export const SeverityButton: React.FC<SBProps> = props => {
    
    return (
        <Container
            background={props.background}
            opacity={props.opacity}
            borderBoxShadow={props.borderBoxShadow}
            onClick={props.onClick}
        >{props.title}</Container>
    );
};