import React, { useState, useEffect, useRef } from 'react';
import { useVoice } from '../hooks/useVoice';
import { uniqueId } from 'lodash';
import { useSelection } from '../hooks/useSelection';
import { SeverityButton } from './SeverityButton';

type Severity = {id: string, severity: string, background: string, title: string};

const severityButtons: Severity[] = [
    {id: uniqueId(), severity: "important", background: "green", title: "Important"},
    {id: uniqueId(), severity: "concerning", background: "blue", title: "Concerning"},
    {id: uniqueId(), severity: "alarming", background: "red", title: "Alarming"},
    {id: uniqueId(), severity: "standard", background: "black", title: "Standard"}
];

type Transcription = {severity: string, transcript: string};

export const VoiceView: React.FC = () => {
    const voice = useVoice();
    const selectableSeverityButtons = useSelection(severityButtons, false);
    const scrollToBottomRef = useRef<HTMLDivElement>(null);

    const [severity, setSeverity] = useState<string>("standard");
    const [finished, setFinished] = useState<boolean>(false);
    const [transcriptions, setTranscriptions] = useState<Transcription[]>([]);
    const [finalTranscript, setFinalTranscript] = useState<string>("");

    useEffect(() => {
        if (finished) setFinalTranscript(JSON.stringify(transcriptions));
    }, [finished, transcriptions]);

    useEffect(() => {
        scrollToBottomRef.current?.scrollIntoView({behavior: 'smooth'});
    }, [voice.transcript, finalTranscript]);

    const addPartialTranscript = (transcription: Transcription) => {
        setTranscriptions([
            ...transcriptions,
            transcription
        ]);
    };

    const saveTranscript = () => {
        switch (severity) {
            case "important":
                addPartialTranscript({severity: "important", transcript: voice.transcript});
                break;
            case "concerning":
                addPartialTranscript({severity: "concerning", transcript: voice.transcript});
                break;
            case "alarming":
                addPartialTranscript({severity: "alarming", transcript: voice.transcript});
                break;
            case "standard":
                addPartialTranscript({severity: "standard", transcript: voice.transcript});
                break;
        }
    }

    const startNewTranscript = (severity: string) => {
        saveTranscript();
        voice.resetTranscript();
        setSeverity(severity);
    };

    const finalizeTranscript = () => {
        setFinished(true);
        saveTranscript();
        setSeverity("standard");
        voice.stopListening();
    };

    return (
        <>
            <div id="text-container" style={{paddingLeft: "20px", paddingRight: "20px", paddingBottom: "20px"}}>
                <p>Microphone: {voice.listening ? 'on' : 'off'}</p>
                {!finished && <div>
                    {!voice.listening && <p>Press the Start button and begin speaking.</p>}
                    {voice.listening && <p>Tap an importance level at the bottom to begin marking the conversation as such. Tap finish when the visit is complete.</p>}
                </div>}
                <hr />
                <div style={{marginTop: "20px"}}>
                    {transcriptions.map(transcription =>
                        <p key={uniqueId()} className={transcription.severity}>{transcription.transcript}</p>
                    )}
                </div>
                {!finished && <p className={severity}>{voice.transcript}</p>}
                <p>{finalTranscript}</p>
                <div ref={scrollToBottomRef}></div>
            </div>
            {voice.listening && !finished && <div className="controls-container">
                {selectableSeverityButtons.selection.map(severityButton => 
                    <SeverityButton
                        key={severityButton.element.id}
                        title={severityButton.element.title}
                        background={severityButton.element.background}
                        opacity={severityButton.selected ? 0.7 : 1}
                        borderBoxShadow={severityButton.selected ? "inset 0 -20px 0 -10px white" : "none"}
                        onClick={() => {startNewTranscript(severityButton.element.severity); selectableSeverityButtons.selectElement(severityButton.element.id);}}
                    />
                )}
                <div className="finish-button" style={{background: "lightblue"}} onClick={finalizeTranscript}>Finish</div>
            </div>}
            {!voice.listening && <div className="controls-container">
                {!finished && <div className="start-finish-box" style={{background: "green", cursor: "pointer"}} onClick={() => {voice.startListening(); selectableSeverityButtons.selectElement(severityButtons[3].id);}}>Start</div>}
                {finished && <div className="start-finish-box" style={{background: "lightgreen"}}>Shadow visit sent successfully</div>}
            </div>}
        </>
    );
};