import React from 'react';
import './App.css';
import { VoiceView } from './components/VoiceView';

export default function App() {

  let getCurrentDate = (separator = '/') => {

    let newDate = new Date()
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    
    return `${month}${separator}${date}${separator}${year}`;
  }

  return (
    <div className="App">
      <header className="App-header">
        <div id="top-box">
          <div className="shadow-title">Shadow Speech</div>
          <div className="patient-info">Patient ID: 2024</div>
          <div className="patient-info">{getCurrentDate()}</div>
        </div>
        {/* <AwsTranscribeSocket/> */}
        {/* <AssemblyAI/> */}
        {/* <AwsTranscribe/> */}
        <VoiceView/>
      </header>
    </div>
  );
}